.container{
    width: 400px;
    margin: auto;
    padding: 22px;
    background-color: rgb(112, 202, 233);
    text-align: center;
    margin-top: 55px;
    border-radius: 6px;
}
.container input{
    width: 70%;
    padding: 8px;
    font-size: 22px;
    border: none;
}
.addTask button{
    width: 80px;
    background-color: rgb(5, 92, 190);
    color: white;
    font-size: 22px;
    border: none;
    cursor: pointer;
}
.addTask{
    display: flex;
    gap: 6px;
    justify-content: center;
}
.task{
    text-align: left;
    margin-left: 15px;
    font-size: 25px;
}
.task-div i{
    position: relative;
    left: -40px;
    font-size: 22px;
    cursor: pointer;
}
.task-div{
    display: flex;
    justify-content: space-between;
    align-items: center;
   
}